.fund-container {
    display: block;
    width: 100%;
    border: 0;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 3px 5px 0px;
    margin-bottom: 10px;
    border-radius: 4px;
}

.fund-selector {
    display: table-cell;
    padding: 0;
    vertical-align: top;
    background: rgba(251, 246, 246, 0.7686274509803922);
}

.fund-details {
    display: table-cell;
    width: 100%;
    padding-left: 10px;
}

.field-label {
    text-transform: uppercase;
    margin-bottom: 2px;
    font-size: 14px;
    font-weight: 600;
}

.field-details {
    font-weight: 500;
    font-size: 12px;
}

.category {
    text-transform: uppercase;
}
.sidebar.open {
    left: 0;
}

.sidebar {
    max-width: 320px;
    width: 100%;
    height: 100vh;
    background: rgb(38, 38, 38);
    position: fixed;
    left: -320px;
    top: 0;
    z-index: 101;
    -webkit-transition: left 0.5s ease-out;
    transition: left 0.5s ease-out;
}

.close-button {
    position: absolute;
    left: 10px;
    top: 13px;
    width: 24px;
    height: 24px;
}

.sidebar-header {
    text-align: center;
    color: #fff;
    text-transform: uppercase;
    font-size: 24px;
    margin-top: 14px;
    border-bottom: 1px solid #fff;
    padding-bottom: 14px;
}

.close-button:before, .close-button:after {
    position: absolute;
    left: 10px;
    content: ' ';
    height: 24px;
    width: 2px;
    background-color: #c635d7;
}

.close-button:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.close-button:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.filter-container {
    color: #fff;
    padding: 5px 10px;
}

.filter-category {
    text-transform: uppercase;
    border-bottom: 1px solid #818181;
    padding-bottom: 5px;
}

.filter-value-container {
    position: relative;
}

.filter .mt-checkbox {
    width: 100%;
    text-align: left;
    position: relative;
}

.filter .mt-checkbox-label {
    text-align: left;
    text-transform: uppercase;
    font-weight: 500 !important;
    padding: 0px 25px;
}

.filter .mt-checkbox-label:before {
    left: 0;
    top: 4px;
    background: #fff;
}

.filter .mt-checkbox-label:after {
    left: 5px;
    top: 5px;
}
.funds-list {
    margin-top: 65px;
}
.fund-search {
    display: inline-block;
    margin-right: 10px;
    max-width: 250px;
    width: 250px;
}
.funds-container {
    margin-top: 65px;
    overflow: hidden;
}

.scrollable-table {
    display: table;
    width: 100%;
    overflow-x: scroll;
}

.field-header {
    display: table-header-group;
    background: #4a4a4af5;
    color: #fff;
}
.field-body {
    display: table-row-group;
}

.field-row {
    display: table-row;
}

.field, .field-value {
    min-width: 150px;
    display: table-cell;
    vertical-align: middle;
    padding: 10px;
    border-right: 1px solid #fff;
}
.field {
    padding: 15px 10px;
}

.field-data {
    font-size: 15px;
    font-weight: bold;
    text-transform: uppercase;
    padding: 0;
    margin: 0;
}
.field-row:nth-child(even) {
    background: #f3f3f3;
}
.parent {
    position: relative;
    width: 100%;
    overflow-x: scroll;
}
